/*=========== DEVOPS css ==============*/

.devops .seo_banner_content h1{
  font-size: 58px;
    line-height: 67px;
}

.devops p {
    color: #6A728F;
}

.devops_benefits_info {
  margin-top: 80px;
}

.devops_how_info {

  padding: 70px 35px;
  z-index: 2;
  position: relative;
}

.devops_part_area .devops_part_item {
    text-align: center;
    width: 50%;
    padding: 0px 30px;
  }
  
  .devops_part_area .devops_part_item h4 {
    font-size: 45px;
    font-weight: 600;
    line-height: 1.3em;
    color: #9529F9;
    margin: 36px 0px 25px;
  }


  .devops  h3 {
    font-size: 30px;
    margin: 0px 0px 25px;
    color: #9529F9;
}

.devops_part_item img {
  width: 220px;
}


.software_featured_item  {
    margin: 0px 20px 30px;
  }


  
  .devops_practices_item {
    padding: 25px 60px 0px;
    background : linear-gradient(-30deg, rgba(149, 41, 249, 1) 0%, rgba(127, 9, 249, 1) 100%);
  }  
  .devops_practices_item .devops_practices_bg {
    text-align: center;
  }
  
  .devops_practices_item .devops_practices_content {
    padding: 45px 55px;
  }
  
  .devops_practices_item .devops_practices_content h3 {
    font-size: 30px;
    font-weight: 600;
    line-height: 40px;
    color: #fff;
    margin-bottom: 18px;
  }
  
  .devops_practices_item .devops_practices_content p {
    color: #fff;
    margin-bottom: 32px;
  }
  
  .devops_practices_item .devops_practices_content .seo_btn {
    border-radius: 45px;
    background: #fff;
    border: 1px solid #ffffff;
    color: #000000;
    padding: 7px 44px;
  }
  
  .devops_practices_item .devops_practices_content .seo_btn:hover {
    -webkit-box-shadow: 0px 20px 30px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 20px 30px 0px rgba(0, 0, 0, 0.1);
  }


  /*=========== SCRUM css ==============*/



  .scrum_artifacts_area {
    background : linear-gradient(90deg, rgba(244, 207, 87, 1) 0%, rgba(252, 115, 70, 1) 99.09%);
    color: #fff;
  }

  .scrum_artifacts_area  .seo_title h1, .scrum_artifacts_area  .seo_title p {
    color: #fff;
  }


  
  .scrum_artifacts_content h5{
    font-size: 40px;
      line-height: 1.09375;
      font-weight: 600;
      letter-spacing: .011em;
      color: #fff;
      margin-bottom: 18px;
      transition: color 0.2s linear;
      display: block;
  }
  
  .scrum_artifacts_content .txt_artif{
      padding-right: 80px;
      font-weight: 500;
  }

  .scrum_team_area {

    padding: 95px 0px;
  }
  
  .scrum_team_inner {
  
    position: relative;
    z-index: 1;
  }
  
  .scrum_team_item {
    border-radius: 6px;

    /* -webkit-box-shadow: 0px 3px 10px 0px rgba(38, 59, 94, 0.1);
    box-shadow: 0px 3px 10px 0px rgba(38, 59, 94, 0.1); */
    height: 100%;
    padding: 40px 20px;
  }
  
  .scrum_team_item img, .software_featured_item img {
    margin-bottom: 35px;
    width: 120px;
  }


  
  .scrum_team_item h5 {
    
    font-size: 30px;
    line-height: 1.09375;
    font-weight: 600;
    letter-spacing: .011em;
    color: #F7AC40;
    margin-bottom: 18px;
    -webkit-transition: color 0.2s linear;
    -o-transition: color 0.2s linear;
    transition: color 0.2s linear;
    display: block;
  }

  .scrum_team_item p {
    font-size: 18px;
    color: #6A728F;
    font-weight: 500;
  }

  

  .scrum_events_tab .nav-item .nav-link.active h3{
    color: #fff;
    font-weight: 700;
  }

  
  .scrum_events_tab_content {
    margin-top: 20px;
    padding: 15px;
  }
  
  
  .scrum_events_tab_content .show .scrum_events_tab_img .scrum_event_img{
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }
  
  .scrum_events_tab_img {
  
    padding: 40px 0px;
    z-index: 1;
  }
  
  .scrum_events_tab_img .text-scrum {
    padding-right: 30px;
  }
  
  
  .scrum_events_tab_img .text-scrum p {
    font-size: 18px;

    color: #6A728F;
    font-weight: 500;
  }
  
  .scrum_events_tab_img .scrum_event_img {
    
    right: 0;
    bottom: 0;
    -webkit-transform: translateX(30px);
    -ms-transform: translateX(30px);
    transform: translateX(30px);
    opacity: 0;
    -webkit-transition: all 0.5s linear;
    -o-transition: all 0.5s linear;
    transition: all 0.5s linear;
  }


/*=========== scrum_events_area css ==========*/
.scrum_events_tab {
  border: 0px;
  margin-bottom: 50px;
}

.scrum_events_tab .nav-item {
  width: calc(100% / 4);
  padding: 0px 15px;
  margin: 0px;
  text-align: center;
}

.scrum_events_tab .nav-item .nav-link {
  border-color: #F7AC40;
  padding: 20px 0px;
  border-radius: 4px;
  border: 2px solid #F7AC40;
  -webkit-transition: all 0.3s linear;
  -o-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

.scrum_events_tab .nav-item .nav-link .icon {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  font-size: 30px;

  color: #fff;
  margin-bottom: 15px;
}

.scrum_events_tab .nav-item .nav-link h3 {
  font-size: 18px;

  color: #F7AC40;
  font-weight: 500;
  white-space: pre-line;
  margin-bottom: 0;
}

.scrum_events_tab .nav-item .nav-link.active {
  border: 0px solid transparent;
  background : linear-gradient(90deg, rgba(244, 207, 87, 1) 0%, rgba(252, 115, 70, 1) 99.09%);
}

/*========RESPONSIVE==========*/

@media (max-width: 667px) {

.seo_banner_content h1 {
  font-size: 50px;
  line-height: 1.1;

}

.scrum_artifacts_area .seo_title h1 {
  font-size: 46px;

}
 
.scrum_artifacts_content h5 {
  font-size: 30px;
} 
  
}
  
