.home_banner_area {
    height: 100%;
    min-height: 100vh;
    position: relative;
    overflow: hidden;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background: url("../../img/sliderImg/Bannerprincipal_Desktop.png") no-repeat scroll center bottom;
    background-size: cover;
}

.home_banner_content {
    height: 270px;

}

.home_banner_content h1 {
    font-size: 95px;
    font-family: 'Karla', sans-serif;
    font-weight: bold;
    line-height: 0.9;
    text-align: left;
    background: -webkit-linear-gradient(0deg, #2E6298, #1d8dc7 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 0;
    padding-top: 10px;
    letter-spacing: -5px;
}



@media (max-width: 667px) {

    .home_banner_area {
        width: 100%;
        min-height: 51vh;
        overflow: hidden;
        z-index: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        background: url("../../img/sliderImg/bannerMobile.png") no-repeat scroll center bottom;
    }

    .home_banner_content h1 {
        font-size: 55px;
        width: 75%;
    }
}

