

.contacts .seo_banner_content p {
    font-size: 20px;
    font-weight: 650;
}

.contacts {
    color: #F6F6F9;
}

.contacts::before {
    content: "";
    background: url("../../img/home/fondo-home.png") no-repeat scroll center bottom;
    position: absolute;
    left: 0;
    width: 60%;
    height: 120%;
    background-size: cover;
    z-index: 0;
    top: 50%;
  } 

.img-banner-contact {
    bottom: -69.5%;
    right: 0;
    position: absolute;
}

.contacts .form-control , .contacts .custom-select{
    height: 45px;
    padding: 10px 15px;
    font-weight: 600;
    border-radius: 20px;
}

.contacts textarea.form-control {
    height: 150px;
}

.contact_info_area {
    background: url("../../img/banner/bannerContacto.png") no-repeat scroll bottom;
    background-size: 800px 700px;
    padding: 80px 0px 100px;
    background-repeat: no-repeat;
    background-position: right;
    background-position-y: bottom;
}


@media (max-width: 667px) { 

    .contacts::before {
        content: "";
        background: url("../../img/home/fondo-home.png") no-repeat scroll center bottom;
        position: absolute;
        left: 0;
        width: 100%;
        height: 50%;
        background-size: cover;
        z-index: 0;
        top: 50%;
      } 

      .contact_info_area {
        background-size: 460px 350px;
        background: url("../../img/banner/bannerContactoMovil.png") no-repeat scroll bottom;
    }

    
}