.sliderDreamcoders {
  padding: 50px 0 80px;
}

#SliderCopy .mySwiper .swiper-slide {
  background-size: cover;
  background-position: center;
  height: calc((100% - 30px) / 2) !important;
  text-align: center;
  font-size: 18px;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  opacity: 0.4;
  
}

#SliderCopy .mySwiper .swiper-slide img {
  border: 0px solid red ;
}

#SliderCopy .mySwiper {
  width: 100%;
  height: 350px;
  margin-left: auto;
  margin-right: auto;
}

#SliderCopy .mySwiper2 {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}

.mySwiper .swiper-slide-thumb-active {
  opacity: 1 !important;
}

.mySwiper .swiper-slider-thumb-active img {
  border: 2px solid red !important;
}

#SliderCopy .mySwiper img {
  border-radius: 100%;
  display: block;
  object-fit: cover;
}

.testimonial_author::before {
  content: "";
  position: absolute;
  left: -70px;
  top: 12px;
  width: 100%;
  height: 100%;
  background: url("../../img/dreamcoders/comillas.png");
  background-position: left;
  background-repeat: no-repeat;
}

.item .a_title {
  color: #E51E79;
}

.testimonial_author {
  padding-top: 20px;
  margin-left: 70px;
}

.item .content {

  font-weight: 500;
  font-style: normal;
  line-height: 40px;
  letter-spacing: 1px;
  color: #fff;
}


.item .name {
  font-family: "Poppins", Sans-serif;
  font-size: 18px;
  font-weight: 400;
  font-style: normal;
  line-height: 26px;
  letter-spacing: 1.5px;
}