.expertise_content::before {
    content: "";
    width: 85%;
    height: 75%;
    background: rgba(230, 230, 230, 1);
    opacity: 0.5;
    position: absolute;
    display: inline-block;
    left: 8%;
    border-radius: 100px;
    z-index: 0;
    margin-bottom: 30px;
    transform: perspective(600px) skewY(3deg) rotateX(343deg) rotateY(2deg)
    
}

