/*==== RESPONSIVE=======*/


@media (max-width: 667px) { 

    .expertise_content::before {
        display: none;   
    }
    .expertises_items {
        background: rgba(230, 230, 230, 0.5);
        border-radius: 80px;
    }
    .expertise_area {
        padding: 80px 35px 10px;
    }
    .our_history_area .container .row:nth-child(2){
        flex-direction: column-reverse;
    }
    .our_history {
      position: relative;
      z-index: 0;
      min-height: 75%;
      border-radius: 40px;
      height: auto;
      width: 100%;
      padding: 40px 30px;
    }

    .our_history_area .img_fondo_content {
      display: none;
    }
    .our_history_content img {
      width: 100%;
    }
    .our_values_content .min-vh-100 {
    min-height: 70vh!important;
    }



    .clients_content .humanIcon {
      position: absolute;
      right: 0;
      width: 70%;
      top: 35%;
    }

    .cuadroClients {
      margin: .1rem!important;
    }

    .our_values_content .mt-5 {
      margin-top: 1rem!important;
      padding-right: 1rem!important;
      line-height: 1.4em;
      font-size: 27px;
    }
    .our_history_content .img-fluid {
      background: linear-gradient( 90deg, rgba(250, 250, 176, 1) 0.12%, rgba(147, 253, 230, 1) 100%);
      border-radius: 20%;
    }
    .about .bg_figuras {
      display: none;
    }

    .valores .performance, .valores .innovacion, .valores .motivacion {
      margin-bottom: 20px;
    }


    .our_values_area {
      padding: 0px;
  }

  .our_values_content .pl-5 {
    padding-left: 1rem!important;
  }

  .our_values_content .seo_title h1, .clients_content .seo_title_area .seo_title h1 {
    font-size: 40px;
  }

  .seo_title_area .seo_title h1 {
    font-size: 45px;
}

