.jobsVideo-background-video-embed {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    width: 1425px;
    height: 801.562px;
    max-width: none;
}

#jobsTeam .ex_team_item .hover_content {
    padding-bottom: 15px;
    background-color: transparent;
    padding: 0px 0px;
    align-items: flex-end;
}

.jobsVideo embed, .jobsVideo iframe, .jobsVideo object, .jobsVideo video {
    max-width: 100%;
    width: 100%;
    margin: 0;
    line-height: 1;
    border: none;
}

.jobsVideo-background-video-container {
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    position: absolute;
    overflow: hidden;
    z-index: 0;
    direction: ltr;
    transition: opacity 1s;

}

.jobsVideo-section {
    margin-top: 50px;
    margin-bottom: 0px;
    padding: 300px 0px 220px 0px;
    position: relative;
    background-size: cover;
}

.rol-title {
    position: absolute;
    width: auto;
    margin-top: -30px;
    padding: 2px 15px;
    display: flex;
    justify-content: center;
}

.jobsSingleBanner h1::after {
    content: "";
    background: url("../../img/home/fondo-home2.png") no-repeat scroll center bottom;
    
    display: inline-block;
    width: 65px;
    height: 65px;
    background-size: cover;
    z-index: 0;
    right: 0;
    top: 0;
}



/*----------- BANNER ---------*/

.banners_job {
    background: linear-gradient(90deg,#70149C,#4813F7);
    background-size: cover;
    background-position: unset;
}

#bannerInicialJob {
    background: url("../../img/banner/banner-carreras.jpg") no-repeat scroll center bottom;
    background-size: cover;
  }



.jobs_content2 .media {
    border-radius: 20px;
    padding: 20px;
    height: 430px;
    transform: translateY(10px);
    transition: all 0.3s linear;
}

.jobs_content2 .media-body {
    border: 2px solid #fff ;
    padding: 50px 20px;
    border-radius: 20px;
    height: 100%;
    width: 100%;
}
.bg_color1 { background: linear-gradient(0deg, rgba(0, 134, 94, 1) 0%, rgba(0, 220, 94, 1) 100%);}
.bg_color2 { background: linear-gradient(0deg, rgba(104, 0, 212, 1) 0%, rgba(110, 85, 193, 1) 100%);}
.bg_color3 { background: linear-gradient(0deg, rgba(0, 162, 170, 1) 0%, rgba(30, 206, 255, 1) 100%);}
.bg_color4 { background: linear-gradient(0deg, rgba(1, 111, 255, 1) 0%, rgba(78, 123, 223, 1) 100%);}
.bg_color5 { background: linear-gradient(0deg, rgba(254, 100, 34, 1) 0%, rgba(255, 159, 1, 1) 100%);}
.bg_color6 { background: linear-gradient(0deg, rgba(174, 69, 127, 1) 0%, rgba(235, 33, 241, 1) 100%);}

.color1 { color: rgba(0, 134, 94, 1)!important;}
.color2 { color: rgba(104, 0, 212, 1)!important;}
.color3 { color: rgba(0, 162, 170, 1) !important;}
.color4 { color: rgba(1, 111, 255, 1) !important;}
.color5 { color: rgba(254, 100, 34, 1)!important;}
.color6 { color: rgba(174, 69, 127, 1)!important;}

.border_color1 { border-color: rgba(0, 134, 94, 1)!important;}
.border_color2 { border-color: rgba(104, 0, 212, 1)!important;}
.border_color3 { border-color: rgba(0, 162, 170, 1) !important;}
.border_color4 { border-color: rgba(1, 111, 255, 1) !important;}
.border_color5 { border-color: rgba(254, 100, 34, 1)!important;}
.border_color6 { border-color: rgba(174, 69, 127, 1)!important;}


:root {  
    --text: #fff; 
    --shadow: #ffffff3c; }

.ex_team_item:hover .hover_content h3 {  
    color: var(--text);  
    text-shadow: 8px -8px var(--shadow), 16px -16px var(--shadow), 24px -24px var(--shadow);
}

#jobsTeam .ex_team_item .team_content {
    bottom: 0;
    padding: 17px 0px;
    transition: all 0.4s ease;
}

#jobsTeam .ex_team_item:hover {
    transform: scale(1.14);
    transition: all 0.3s ease-out;
}

/*-----SLIDER SINGLE PERFIL*/


#sliderJobs .swiper-pagination-bullet {
    display: inline-block;
    width: 40px;
    height: 10px;
    margin: 0 9px;
    background: #333;
    border-radius: 4px;
    cursor: pointer;
}



/*-----FIN SLIDER SINGLE PERFIL*/


@media (max-width: 667px) {

    .jobs_content2 .media {
        border-radius: 20px;
        padding: 20px;
        height: auto;
        margin: 5px 10px;
    }

    .ex_team_item .team_content h3 {
        font-size: 15px;
    }
    .ex_team_item:hover .hover_content h3 {
        font-size: 35px;
    }

    #bannerFinalJob h2{
        text-align: center !important;
        padding-bottom: 20px;
    }

    .jobsSingleBanner h1::after {
        width: 40px;
        height: 40px;
    }
    

}

