    font-weight: 700;
    
}

.swiper-blog-home {
  overflow: hidden;
}  



.portfolio_area {
    overflow: hidden;
    padding-top: 30px;

  }
  
  
  .blog_area {
    padding: 50px 0px 10px;
  }

  .blog_area .seo_title p {
      text-align: left;
      margin-left: inherit;
      font-weight: 500;
      font-size: 25px;
      max-width: 100%;
  }
  
  .post_area {
    padding: 0px 0px 10px;
  }

  /*============= portfolio area css ============*/

  
  .portfolio_filter .work_portfolio_item {
    font-size: 18px;
    font-weight: 500;
    color: #fff;
    display: inline-block;
    border-radius: 3px;
    -webkit-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
    position: relative;
    cursor: pointer;
    padding: 10px 20px;
    background: #757C99;
    
  }


  .portfolio_filter .all {
    background: #757C99;
  }

  .portfolio_filter .digital {
    background: #4067FB;
  }

  .portfolio_filter .innovacion {
    background: #8E278B;
  }

  .portfolio_filter .up-to-date {
    background: #00A251;
  }

  .portfolio_filter .work_portfolio_item.active {
    font-weight: 600;
  }
  .portfolio_filter .All.active {
    color: #757C99;
    border: 1px solid #757C99;
    background: transparent;
  }

  .portfolio_filter .digital.active {
    color: #4067FB;
    border: 1px solid #4067FB;
    background: transparent;
  }
  .portfolio_filter .innovacion.active {
    color: #8E278B;
    border: 1px solid #8E278B;
    background: transparent;
  }
  .portfolio_filter .up-to-date.active {
    color: #00A251;
    border: 1px solid #00A251;
    background: transparent;
  }

  
  .portfolio_filter .work_portfolio_item + .work_portfolio_item {
    margin-left: 40px;
  }


