/*---------- JOBS ------------*/

  .jobs_area {

    padding: 50px 0px;
  }
  
  .jobs_content {
    max-width: 970px;
    margin: 0 auto;
  }
  
  .jobs_tab_content {
    max-width: 970px;
    margin: 0 auto;
  }

  .jobs_tab_content .media {
    border-radius: 6px;
    background-color: #f8f9fa;
    border: 2px solid #00233f;
    padding: 40px;
    margin-top: 20px;
    -webkit-transform: translateY(10px);
    -ms-transform: translateY(10px);
    transform: translateY(10px);
    -webkit-transition: all 0.3s linear;
    -o-transition: all 0.3s linear;
    transition: all 0.3s linear;
  }
  
  
  .jobs_tab_content .media .media-left {
    margin-right: 70px;
  }
  
  
  
  .jobs_tab_content .media .media-left p {
    display: block;
    font-size: 16px;
    line-height: 20px;
    color: #2c2c51;
    margin-top: 6px;
  }
  
  .jobs_tab_content .media .media-left p i {
    color: #00233f;
    padding-right: 8px;
  }

  .banner_job .seo_banner_content h1 {
    background-color: #fff;
    padding: 5px 0;
}
  
  .banner_job {
    background: url("../../img/banner/bannerJob.png") no-repeat scroll center bottom;
    background-size: cover;
    margin-top: 60px;
    background-position: unset;
  }
  
  
  .jobs_tab_content .media .media-body h2 {
    font-weight: 500;
    margin-bottom: 15px;
    font-size: 30px;
    font-weight: 600;
    color: #246DFB;
    margin-bottom: 20px;
    -webkit-transition: all 0.3s linear;
    -o-transition: all 0.3s linear;
    transition: all 0.3s linear;
  }
  
  .jobs_tab_content .media .media-body h3 {
    font-weight: 500;
    margin: 15px 0px 5px;
    font-size: 20px;
    font-weight: 600;
    color: #2c2c51;
  }
  
  
  .jobs_tab_content .media .media-body {
    border-left: 0px solid #00233f;
    border-top: 1px solid #00233f;
    padding-left: 0px;
    padding-top: 20px;
}
  
  .jobs_tab_content .media .media-body p {
    margin: 5px 0px;
    font-weight: 500;
  }
  
  .jobs_tab_content .media .learn_btn {
    font: 400 15px/25px "Futura", sans-serif;
  }
  
  
  .jobs_tab_content .media .learn_btn:before {
    content: "";
    width: 100%;
    left: 0;
    bottom: 0;
    height: 1.5px;
    background: #000;
    position: absolute;
    transition: background 0.3s linear;
  }
  
  .jobs_tab_content .media .learn_btn:hover:before {
    background: #0056b3;
  }
  
  .jobs_content .jobs_tab {
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    padding-bottom: 20px;
    border: 0px;
  }
  
  .jobs_content .jobs_tab .nav-item {
    margin: 0;
  }
  
  .jobs_content .jobs_tab .nav-item .nav-link {
    text-align: center;
    border: 1px solid #00233f;
    border-radius: 45px;
    padding: 6px 58px 8px;
    -webkit-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
  }
  
  .jobs_content .jobs_tab .nav-item .nav-link h5 {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 0;
    color: #00233f;
  }
  
  .jobs_content .jobs_tab .nav-item .nav-link h5 span {
    font-weight: 400;
    font-size: 14px;
    display: block;
    padding-top: 5px;
  }
  
  .jobs_content .jobs_tab .nav-item .nav-link.active, .jobs_content .jobs_tab .nav-item .nav-link:hover {
    background: #00233f;
  }
  
  .jobs_content .jobs_tab .nav-item .nav-link.active h5, .jobs_content .jobs_tab .nav-item .nav-link:hover h5 {
    color: #fff;
  }
  
  .jobs_content .jobs_tab .nav-item + .nav-item {
    margin-left: 20px;
  }
  
  
.jobs_area .subTitle_job {
    padding: 55px 60px 30px ;
    background : linear-gradient(-30deg, rgba(6, 33, 180, 1) 0%, rgba(64, 103, 251, 1) 99.33%);
    border-radius: 40px;
    color: #fff;
}

.jobs_area .subTitle_job p {
    text-align: left;
    color: #fff;
    margin-left: 0;
    max-width: 100%;
}