/* $phone-ratio: 2.01; */

.Phone {
  height: 30.15rem; /* 15rem * $phone-ratio; */
  margin: 0 auto;
  position: relative;
  transform: rotate(90deg) scale(0);
  width: 15rem;
}

@media (min-width: 600px) {
  .Phone {
    height: 50.25rem; /* 25rem * $phone-ratio; */
    width: 25rem;
  }
}

.Phone-svg {
  display: block;
  height: 100%;
  position: relative;
  width: 100%;
  z-index: 10;
}

.Phone-content {
  height: 74%;
  left: 8%;
  overflow: hidden;
  position: absolute;
  top: 13.5%;
  width: 85%;
  z-index: 0;
}

.Phone-contentPlx {
  background: var(--blue);
  height: 100%;
  width: 100%;
}

.Phone-contentSection {
  background: var(--red);
  color: var(--white);
  font-size: 3rem;
  font-weight: 300;
  height: 100%;
  padding-top: 75%;
  text-align: center;
  width: 100%;
}

.Phone-contentSection:nth-child(2) {
  background: var(--blue);
}

.Phone-contentSection:nth-child(3) {
  background: var(--green);
}

@media (min-width: 600px) {
  .Phone-contentSection {
    font-size: 5rem;
  }
}

.Links {
  border-radius: 50%;
  height: 12rem;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
  width: 12rem;
}

