.home-services-title {
    position: relative;
    padding-bottom: 90px;
    z-index: 2;
  }


  .homeServices {
    padding-top: 80px ;
  }
  .home-services-title::before {
    content: "";
    background: url("../../img/home/fondo-title-service.png") no-repeat scroll center bottom;
    position: absolute;
    left: 40px;
    width: 100%;
    height: 100%;
    background-size: cover;
    z-index: 0;
    top: 0px;
  }
.stratup_testimonial_info {
    height: 576px;
    position: relative;
  }
.stratup_testimonial_info::after {
    content: "";
    position: absolute;
    background: linear-gradient(-66.23deg, rgba(6, 33, 180, 1) 0%, rgba(36, 109, 251, 1) 99%);
    width: 710px;
    height: 300px;
    border-radius: 150px 0 0 150px;
    transform: rotate(356deg);
    bottom: 12%;
    box-shadow: 4px -12px 1px 4px rgb(12 27 120 / 40%);  
  }
  .stratup_testimonial_info::before {
    content: '';
    width: 300px;
    height: 300px;
    -moz-border-radius: 7.5px;
    -webkit-border-radius: 7.5px;
    border-radius: 50%;
    background-color: #32A6DA;
    position: absolute;
    top: 15%;
    box-shadow: 14px 11px 1px 4px rgb(12 27 120 / 40%);
    left: 15px;
  }


  @media (max-width: 667px) {
    .ServItem .w-50 {
      width: 75%!important;
    }
    #home-services .swiper-slide {
      height: 85%;
    }
    .home-services-title {
    position: relative;
    padding-bottom: 10px;
    }
    .home-services-title h2 {
      font-size: 45px;
    }
    .stratup_testimonial_info::before {
      top: 55px;
    }
    .stratup_testimonial_info::after {
      bottom: 12%;
      width: 405px;
      transform: rotate(360deg);
    }

  }

  /*---POSIBLEMENTE NO SE USE----*/

  .home-services-title .prev, .home-services-title .next {
    position: absolute;
    bottom: 0;
    width: 50px;
    height: 50px;
    border: 1px solid #e0e4ed;
    text-align: center;
    font-size: 14px;
    color: #677294;
    border-radius: 50%;
    line-height: 48px;
    -webkit-transition: all 0.3s linear;
    -o-transition: all 0.3s linear;
    transition: all 0.3s linear;
    cursor: pointer;
    background: transparent;
  }

/*NUEVO SLIDER*/

#home-services .swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  align-items: center;
}

#home-services .swiper-slide {
  text-align: center;
  display: flex;
  align-items: center;
  height: 70%;
  position: relative;
  transition-property: transform;
  flex-shrink: 0;
  color: #fff;
  flex-wrap: wrap;
  align-content: space-around;
  justify-content: flex-start;
  padding: 0px 10px;
}

#home-services .swiper-button-prev, #home-services .swiper-button-next {
  color: #fff;
  top: 60%;
}