/*----SLIDERS----*/
.swiper {
    width: 100%;
    height: 100%;
  }

#sliderJobs .swiper-slide{ 
  padding-bottom: 50px;
}
  
#sliderJobs .swiper-slide {
    text-align: center;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    flex-wrap:wrap;
  }

#sliderJobs  .swiper-wrapper {
    align-items: center;
  }
  
  
#sliderJobs  .swiper {
    margin-left: auto;
    margin-right: auto;
  }

/* ---- FORM CARERRS ----*/

.banner_landing {
  background: linear-gradient(90deg, rgba(174,253,241,1) 0%, rgba(38,198,218,1 ) 100%);
    background-size: cover;
    background-position: unset;    
}

.form_landing .form-control{
  -webkit-text-fill-color: white !important;
}
.form_landing .form-control, .form_landing .custom-select {
  padding: 10px 15px;
  font-weight: 600;
  border-radius: 20px;
  height: 45px;
  
}

.form-control.is-invalid, .was-validated .form-control:invalid {
  border-color: #fff !important;
  background-image: none !important;
}

#CareersForm .was-validated .form-check-input:invalid~.form-check-label {
  color: #fff;
}


#CareersForm .form-control, #CareersForm select {
  background-color: transparent;
  border: 1px solid #ffffff !important;
  color: #fff;
}

.btn_form {
  background: #ffffff !important;
  font-size: 15px;
  border: 0px solid #ffffff !important;
  padding: 10px 15px;
  transition: all 0.3s linear;
  display: inline-block;
  border-radius: 20px;
}



.inputfile {

  opacity: 0;
  overflow: auto;
  position: absolute;
  width: 90%;
  height: 60%;

}

.btn-file {
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    display: inline-block;
    padding: 0.625rem 1.25rem;
}
  