#bannerInicialBenefics {
  background: url("../../img/banner/banner-benefits.jpg") no-repeat scroll center bottom;
  background-size: cover;
}

.w-10 {
  width: 8%;
}

.img-icons > .w-10 + .w-10 {
  margin-left: 60px;
}

.benefits_item {
  position: relative;
  text-align: center;
  margin-bottom: 40px;
  overflow: hidden;
}

.benefits_item img {
  max-width: 100%;
}

.benefits_item .team_content {
  bottom: 0;
  background: #9529F9;
  padding: 20px 10px;
  transition: all 0.4s ease;
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 102px;
}



