.service_item_area {
    z-index: 0;
    padding: 60px 0px;   
}

.imgDesa {
    background : linear-gradient(90deg, rgba(6, 33, 180, 1) 0%, rgba(64, 103, 251, 1) 99.33%);
    color: #002EFF !important;
}

.imgConsult {
    background : linear-gradient(90deg, rgba(57, 177, 74, 1) 0%, rgba(52, 171, 72, 1) 20.47%, rgba(38, 152, 68, 1) 49.02%, rgba(15, 123, 61, 1) 82.16%, rgba(0, 104, 56, 1) 100%);
    color: #39B14A !important;
}

.imgApp {
    background : linear-gradient(90deg, rgba(255, 238, 0, 1) 0%, rgba(254, 232, 6, 1) 21.86%, rgba(252, 213, 24, 1) 52.35%, rgba(248, 184, 52, 1) 87.75%, rgba(247, 172, 64, 1) 100%);
    color: #F3901E !important;
}

.imgBd {
    background : linear-gradient(90deg, rgba(102, 45, 141, 1) 6.6%, rgba(142, 39, 139, 1) 100%);
    color: #662D8D !important;
}

.servItems img {
    width: 160%;
}

.services_content {

    /* overflow: hidden; */
    border-radius: 40px;
    margin: auto;
    background: #fff;
}

.servItems .content-item {
    padding: 50px 60px;
}

.servItems .mt-6 {
    margin-top: 6rem;
}

/*==========RESPONSIVE==============*/

@media (max-width: 667px) {
    .servItems .f_size_60 {
        font-size: 40px;
    }

    .servItems .imgServ img {
        width: 80%;
    }

    .servItems .content-item p {
        font-size: 18px;
    }
    .services_content {
        overflow: hidden;
        padding: 30px 30px;}
    }




