.category_item {
    background: url("../../img/blog-grid/blog-banner.jpg") no-repeat scroll center bottom;
    padding: 50px ;
}


.banner_category {
    background: url("../../img/blog-grid/blog-banner.jpg") no-repeat scroll center bottom;
    background-size: cover;
    background-position: unset;
}

.banner_category .seo_banner_content h1 {
    color: #fff;
}

.portfolio_area .breadcrumb {
    background-color: #fff;
}

.category .categoryItem {
    padding: 64px;
    height: 100%;
}

.innovation .seo_banner_content h1, .transformation .seo_banner_content h1 {
    font-size: 65px;
    line-height: 80px;
    color: #fff; 
    font-weight: 700;
}


.category .innovation, section.banner_area.innovation.sec_pad2 {
    background: url("../../img/blog-grid/Banner-innovation.jpg") no-repeat scroll center bottom;
    background-size: cover;
    background-position: bottom;
    color: #FFF;
}

.category .up-to-date, section.banner_area.up-to-date.sec_pad2 {
    background: url("../../img/blog-grid/Banner-stay-up.jpg") no-repeat scroll center bottom;
    background-size: cover;
    background-position: inherit;
}

.category .transformation, section.banner_area.transformation.sec_pad2 {
    background: url("../../img/blog-grid/Banner-tecnology.jpg") no-repeat scroll center bottom;
    background-size: cover;
    background-position: inherit;
    color: #FFF;
}