
.newsletter {
    z-index: 9998 !important;
}

.newsletter .modal-content {
    border-radius: 25px;
}

.newsletter .modal-header {
    border-radius: 25px 25px 0px 0px;
    background: linear-gradient(90deg, rgba(70,0,255,1) 0%, rgba(156,0,166,1 ) 100%);
}

.newsletter .modal-body input{
    box-shadow: 3px 4px 0px 0.7px rgb(0 0 0);
}

.newsletter .modal-body div p::after {
    content: "";
    position: absolute;
    width: 65.5%;
    height: 12.5%;
    background-color: #fff;
    border: 1px solid #000;
    border-radius: 0.3rem!important;
    bottom: 4px;
    left: 67px;
}

:root {
    --transparent: transparent;
}

.newsletter .modal-body button {  
    background-color: rgb(252, 209, 118);
}

@media (min-width: 576px){
.newsletter .modal-dialog {
    max-width: 770px !important;
}

}


@media (max-width: 667px){
    .newsletter .modal-body .col-lg-6{
        text-align: center;
        justify-content: center;
    }

    .newsletter {
        z-index: 9998;
    }

    .newsletter .newsletter-img img{
        width: 55%;
    }
    
    }



